@import url(https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
.movie {
  background-size: 100%;
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transition: 0.5s ease-in-out;
  text-align: center;
}
.movie:hover {
  border-radius: 100%;
}
.movie:hover .movie__title,
.movie:hover .movie__genres {
  display: none;
}
.movie:hover .movie__year,
.movie:hover .movie__rating,
.movie:hover .movie__summary {
  display: block;
}

/* movie__data front */
.movie__title {
  font-size: 28px;
}
.movie__genres {
  font-size: 24px;
}

/* movie__data back*/
.movie__year {
  display: none;
  font-size: 44px;
}
.movie__rating {
  display: none;
  font-size: 60px;
  letter-spacing: 0px;
  color: var(--main-color);
}
.movie__summary {
  display: none;
  text-align: left;
}

.movies {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-auto-rows: 450px;
}

.loader__container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  z-index: 1;
  position: fixed;
  top: 0;
  color: black;
}

.loader {
  font-size: 6px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: var(--loader-color);
  background: linear-gradient(
    to right,
    var(--loader-color) 10%,
    var(--loader-color-alpha) 42%
  );
  position: relative;
  animation: load3 1.4s infinite linear;
  transform: translateZ(0);
}
.loader:before {
  width: 50%;
  height: 50%;
  background: var(--loader-color);
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
}
.loader:after {
  background: white;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: "";
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
@keyframes load3 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.about__container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
  font-size: 20px;
  color: black;
}

.about_dots__container {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 15vh;
  z-index: -1;
  color: var(--main-color);
  font-size: 60vh;
  letter-spacing: -20vh;
}
.about__dots {
  margin-right: 20vh;
}

.navigation {
  padding: 8px 32px;
  font-size: 42px;
  display: flex;
  justify-content: space-between;
}
.navigation__title {
  margin-top: 6px;
}

.navigations {
  display: none;
  margin-top: 6px;
}
.navigation:hover .navigation__title {
  display: none;
}
.navigation:hover .navigations {
  display: block;
}

.navigation__home {
  margin-right: 16px;
}

.navigation a:hover {
  text-decoration: underline;
}

.navigation__dots {
  color: var(--main-color);
  font-size: 60px;
}

.detail__container {
  height: 100vh;
  display: flex;
  justify-content: center;
}

.detail__poster {
  -webkit-filter: blur(2px);
          filter: blur(2px);
}

.detail__data {
  width: 500px;
  height: 100vh;
  padding: 0 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: absolute;
}
.detail__title {
  font-size: 28px;
  margin-top: 24px;
  margin-bottom: 10px;
}
.detail__genres {
  margin-bottom: 10px;
  font-size: 28px;
}
.detail__data__main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.detail__rating {
  color: var(--main-color);
  font-size: 80px;
}
.detail__summary {
  margin-bottom: 24px;
}

:root {
  --main-color: #ffcd48;
  --loader-color: rgb(255, 205, 72);
  --loader-color-alpha: rgba(255, 205, 72, 0);
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Raleway", sans-serif;
  background-color: white;
  font-weight: 600;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

.highlighter {
  background-color: black;
  color: white;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 4px 8px;
}

body::-webkit-scrollbar {
  width: 12px;
}
body::-webkit-scrollbar-thumb {
  background-color: black;
}
body::-webkit-scrollbar-track {
  background-color: white;
}

